import {BuffRange, BuffSource} from "./Buff";
import {getIncome} from "./Level";


class Building{
    constructor(name,rarity,type,baseMoney){
        this.disabled = false;
        this.level = 1;
        this.star = 0;
        this.quest = 0;
        this.buffs = [];  //
        this.BuildingName = name;
        this.rarity = rarity;
        this.BuildingType = type;
        this.baseMoney = baseMoney;
    }

    calculation(buffs){
        let addition = {};
        let money = this.money;
        addition[BuffRange.Online] = money;
        addition[BuffRange.Offline] = money / 2;
        [BuffSource.Building,BuffSource.Policy,BuffSource.Photo,BuffSource.Quest].forEach((source)=>{
            let buff = buffs.Calculation(source,this);
            Object.keys(buff).forEach((range)=>{
                addition[range] *= buff[range];
            });
        });
        return addition;
    }

    get money(){
        return this.baseMoney * this.multiple * getIncome(this.level); //
    }

    get multiple(){
        switch (this.star) {
            case 1:
                return  1;
            case 2:
                return 2;
            case 3:
                return 6;
            case 4:
                return 24;
            case 5:
                return 120;
            default:
                return 1;
        }
    }

    getBuffValue(buff){
        return buff.buff * this.star;
    }
}

let BuildingRarity = {
    Common:"",
    Rare:"",
    Legendary:""
};

let BuildingType = {
    Residence:"",
    Business:"",
    Industrial:""
};

export {
    Building,
    BuildingRarity,
    BuildingType
}